.container {
  position: relative;
}

.page {
  width: 94%;
  position: absolute;
}

.page-enter {
  opacity: 0;
  transform: scale(1.1);
}

.page-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: opacity 300ms, transform 300ms;
}

.page-exit {
  opacity: 1;
  transform: scale(1);
}

.page-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 300ms, transform 300ms;
}

.page-appear {
  opacity: 0;
}

.page-enter-done {
  opacity: 1;
  transition: transform 2000ms, opacity 2000ms;
}
